.sign-up{
  display: flex;
  flex-direction: column;
  width: 380px;

  .title{
      margin: 10px 0;
  }
}
.checkout-page {
  width: 100%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 60%;
    background-color: violet;
    font-style:italic;
    height: 50px;
    align-items: center;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 20%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
}