.sign-in{
    width: 50%;
    display: flex;
    margin: 20px auto 0;
    flex-direction: column;
  
    .title{
        margin: 10px 0;
    }
    .buttons {
        display: flex;
        justify-content: space-between;
    }
  }
  
  $sub-color: grey;
  $main-color: black;
  
  @mixin shrinkLabel {
    left: -14px;
    font-size: 10px;
    color: $main-color;
  }
  
  .group {
    position: relative;
    margin: 5px 0;
  
    .form-input {
      background: none;
      background-color: white;
      color: $sub-color;
      font-size: 18px;
      padding: 10px 10px 10px 5px;
      display: block;
      width: 40%;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid $sub-color;
      margin: 20px 0;
  
      &:focus {
        outline: none;
      }
  
      &:focus ~ .form-input-label {
        @include shrinkLabel();
      }
    }
  
    input[type='password'] {
      letter-spacing: 0.3em;
    }
  
    .form-input-label {
      color: $sub-color;
      font-size: 12px;
      font-weight: normal;
      position: absolute;
      pointer-events: none;
      left: -20px;
      top: 5px;
      transition: 300ms ease all;
  
      &.shrink {
        @include shrinkLabel();
      }
    }
  }